body{
    margin:auto 0;
    font-family: arial;
}
/* nav */
.main-nav {
  display: flex;
  list-style: none;
  font-size: 0.6em;
  margin: 0;

}

.sticky {
  position:fixed;
  top:0;
  width:100%;
}


@media only screen and (max-width: 600px) {
  .main-nav {
    font-size: 0.5em;
    padding:0;
  }
}

li {
  padding: 20px

}

a{
  color:#f5f5f6;
  text-decoration: none;
}
.push{
  margin-left:auto;
}
/* push conatct to the left end */


.container{
  display: flex;
  align-items: center;
  justify-content: center;
  height:80vh;
    /* 50% of viewport */

}

.grid-wrapper {
  display:grid;
  grid-gap:20px;
  grid-template-columns: repeat(auto-fill,minmax(280px,1fr));
}

.box > img {
  width:100%;
}

.box {
  background-color:#444;
  padding:30px;
  margin:20px;
}

.cover {
  width:20rem;
  margin-top: 3rem;
  color:#FFF;
}

footer {
  text-align: center;
}




.zone {
  /* padding:30px 50px;
  margin:40px 60px; */
  cursor:pointer;
  /* display:inline-block; */
  color:#FFF;
  font-size:2em;
  border-radius:4px;
  border:1px solid #bbb;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.zone:hover {
  -o-box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
  box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
}

/*https://paulund.co.uk/how-to-create-shiny-css-buttons*/
/***********************************************************************
*  Green Background
**********************************************************************/
.green{
  background: #56B870; /* Old browsers */ /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#56B870), color-stop(100%,#a5c956)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, #56B870 0%,#a5c956 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(top, #56B870 0%,#a5c956 100%); /* W3C */
}

/***********************************************************************
*  Red Background
**********************************************************************/
.red{
  background: #C655BE; /* Old browsers */ /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#C655BE), color-stop(100%,#cf0404)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, #C655BE 0%,#cf0404 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(top, #C655BE 0%,#cf0404 100%); /* W3C */
}

/***********************************************************************
*  Yellow Background
**********************************************************************/
.yellow{
  background: #F3AAAA; /* Old browsers */ /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#F3AAAA), color-stop(100%,#febf04)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, #F3AAAA 0%,#febf04 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(top, #F3AAAA 0%,#febf04 100%); /* W3C */
}

/***********************************************************************
*  Blue Background
**********************************************************************/
.blue{
  background: #7abcff; /* Old browsers */ /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#7abcff), color-stop(44%,#60abf8), color-stop(100%,#4096ee)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top, #7abcff 0%,#60abf8 44%,#4096ee 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(top, #7abcff 0%,#60abf8 44%,#4096ee 100%); /* W3C */
}

